$orange: #f05c2a;
$green: #008090;

html {
	width: 100%;
	min-width: 820px;
	background: url(img/bg-left.jpg) 0 0 no-repeat;
	background-attachment: fixed;
	@media (max-width: 1600px) {
		background-image: none;
	}
	@media (min-width: 1600px) {
		background-position: -100px 0;
	}
	@media (min-width: 1750px) {
		background-position: 0 0;
	}
}
body {
	width: 100%;
	font-size: 10px;
	background: url(img/bg-right.jpg) 100% 40px no-repeat;
	background-attachment: fixed;
	font-family: 'HelveticaNeue';
	@media (max-width: 1600px) {
		background-image: none;
	}
	@media (min-width: 1600px) {
		background-position: 100% 40px;
	}
}

@font-face {
  font-family: 'HelveticaNeue';
  src: url("fonts/HelveticaNeue.eot");
  src: url("fonts/HelveticaNeue.eot?#iefix") format('embedded-opentype'), url("fonts/HelveticaNeue.ttf") format('truetype'), url("fonts/HelveticaNeue.svg#HelveticaNeue") format('svg');
  font-weight: 'normal';
  font-style: normal;
}
@font-face {
  font-family: 'HelveticaNeueLight';
  src: url("fonts/HelveticaNeueLight.eot");
  src: url("fonts/HelveticaNeueLight.eot?#iefix") format('embedded-opentype'), url("fonts/HelveticaNeueLight.ttf") format('truetype'), url("fonts/HelveticaNeueLight.svg#HelveticaNeueLight") format('svg');
  font-weight: 'normal';
  font-style: normal;
}

.header-inner {
	max-width: 820px;
	margin: 50px auto;
	text-align: center;
	&>a {
		display: inline-block;
		margin-bottom: 50px;
	}
	img {
		display: block;
		margin: 0 auto;
	}
}
.menu-lang {
	overflow: hidden;
	margin-bottom: 50px;
	h2 {
		font-family: 'HelveticaNeueLight';
		font-weight: 300;
		font-size: 1.9em;
		letter-spacing: 20.5px;
		color: $orange;
		padding: 0;
		margin: 0 -20px 20px 15px;
		text-transform: uppercase;
	}
}
.menu {
	float: left;
	padding: 0 0 0 10px;
	margin: 0;
	width: 80%;
	box-sizing: border-box;
	li {
		float: left;
		list-style: none;
		background-color: $green;
		font-size: 1.8em;
		padding: 5px 25px;
		margin-right: 30px;
		-ms-transform: skewX(-23deg);
		-webkit-transform: skewX(-23deg);
		transform: skewX(-23deg);
		a {
			color: #fff;
			text-decoration: none;
			-ms-transform: skewX(23deg);
			-webkit-transform: skewX(23deg);
			transform: skewX(23deg);
			display: block;
		}
		&.current-menu-item,
		&:hover {
			background-color: $orange;
		}
	}
}
.lang {
	float: right;
	width: 16%;
	background-color: $green;
	font-size: 1.8em;
	padding: 5px 0;
	text-align: center;
	-ms-transform: skewX(-23deg);
	-webkit-transform: skewX(-23deg);
	transform: skewX(-23deg);
	margin-right: 10px;
	a {
		color: #fff;
		text-decoration: none;
		-ms-transform: skewX(23deg);
		-webkit-transform: skewX(23deg);
		transform: skewX(23deg);
		display: block;
	}
	&:hover {
		background-color: $orange;
	}
}
.wrapper {
	margin: 0 auto;
	width: 100%;
	box-sizing: border-box;
	max-width: 820px;
}

.about {
	p {
		font-size: 1.4em;
		font-family: 'HelveticaNeue';
	}
}

footer {
	padding-bottom: 40px;
}
.footer-inner {
	margin: 120px 0 0;
	text-align: center;
	position: relative;
	&:before {
		content: '';
		position: absolute;
		height: 12px;
		background-color: $green;
		width: 600px;
		margin-left: -370px;
		top: -50px;
		-ms-transform: skewX(-23deg);
		-webkit-transform: skewX(-23deg);
		transform: skewX(-23deg);
	}
	&:after {
		content: '';
		position: absolute;
		height: 12px;
		background-color: $orange;
		width: 600px;
		margin-left: -220px;
		top: -30px;
		-ms-transform: skewX(-23deg);
		-webkit-transform: skewX(-23deg);
		transform: skewX(-23deg);
	}
	h4 {
		text-transform: uppercase;
		font-size: 12px;
		font-weight: 400;
		letter-spacing: 5px;
	}
	p {
		font-size: 1.1em;
	}
}


.contact {
	overflow: hidden;
}
.contact-map {
	float: left;
	width: 450px;
	height: 250px;
	iframe {
		border: 0;
		width: 100%;
		height: 100%;
	}
}
.contact-addr {
	float: left;
	width: 250px;
	height: 250px;
	margin-left: 30px;
	position: relative;
	p {
		font-size: 14px;
	}
}
.contact-add-bottom {
	position: absolute;
	bottom: 0;
	width: 100%;
}
